import axios from "axios";
import { ReadError } from "utils/customErrors";
// import url from "./public";


const instance = axios.create({
    baseURL: '',

    // process.env.REACT_APP_API_TEST_URL
    timeout: 60000
});

instance.interceptors.request.use((config) => {
    // let token = localStorage.getItem('token');
    // token = JSON.parse(token).token.split('"').join('');
    config.headers.Authorization = localStorage.getItem('token');
    return config;
});

export default class Service {
    static async getStation() {
        const response = await instance.get('/station');
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }

    static async getDevices(id) {
        const response = await instance.get('/station/device',
            {
                params: {
                    stationId: id
                }
            });
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }

    static async getLastActive(id) {
        const response = await instance.get('/station/lastActive',
            {
                params: {
                    id
                }
            });
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }

    static async login(userName, password) {
        const response = await instance.post('/user/login', {
            login: userName,
            password
        });
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }

    static async getLogin() {
        const response = await instance.get('/user/login');
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }

    static async getProgramPack(id) {
        const response = await instance.get('/programPack',
            {
                params: {
                    id
                }
            }
        );
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }

    static async sendNumberToDevice(value) {
        const response = await instance.post('/sendNumberToDevice', {
            number: value.number,
            programPackId: value.programPackId
        });
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }


    static async getArchive(data) {
        const response = await instance.get('/programPack/archive',
            {
                params: {
                    dateStart: data.dateStart,
                    period: data.period,
                    id: data.id,
                    // divider: 10
                    // func: "avarage"
                }
            }
        );
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }

    static async getActiveAlarm(id) {
        const response = await instance.get('/device/error',
            {
                params: {
                    deviceId: id
                }
            }
        );
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }


    static async getArchiveAlarm(queryData) {
        const response = await instance.get('/device/archiveErrors',
            {
                params: {
                    deviceId: queryData.id,
                    dateStart: queryData.dateStart,
                    period: queryData.period,
                    limit: 100
                }
            }
        );
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }

    static async getProgramPacks(id) {
        const response = await instance.get('/device/programPack',
            {
                params: {
                    deviceId: id
                }
            }
        );
        if (response.data.status === 'error') {
            throw new ReadError(response.data.ru, response.data.number);
        }
        return response.data;
    }

    static async getProgramPackArray(data) {
        const response = await instance.post('/programPackArray',
            data
        );
        if (response.data.status === 'error') {
            throw new Error(response.data.ru);
        }
        return response.data;
    }
}

