
import RequireAuth from 'pages/authentication/RequireAuth';
import { Route, Routes } from 'react-router-dom';
// import Login from './pages/authentication/Login';
import ThemeCustomization from './themes';
//import Routes from 'routes';
import { lazy } from 'react';
import {
  QueryClient,
  QueryClientProvider,
} from 'react-query';
// project import
import Loadable from 'components/Loadable';
import MainLayout from 'layout/MainLayout';
import { looker, root } from 'assets/roles';

// render - dashboard
const Devices = Loadable(lazy(() => import('pages/Devices')));
const Stations = Loadable(lazy(() => import('pages/station')));
const Login = Loadable(lazy(() => import('pages/authentication/Login')));
const TestPage = Loadable(lazy(() => import('pages/testPage')));
const Manufacture = Loadable(lazy(() => import('pages/manufacture')));
const Line = Loadable(lazy(() => import('pages/line')));
const Node = Loadable(lazy(() => import('pages/Node/Index')));
const queryClient = new QueryClient();

function App() {
  return (
    <QueryClientProvider client={queryClient}>
      <ThemeCustomization>
        <Routes>
          <Route path="/" element={<MainLayout />} >
            <Route element={<RequireAuth allowedRoles={looker} />}>
              <Route index element={<Manufacture />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={root} />}>
              <Route path="devices" element={<Devices />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={root} />}>
              <Route path="station" element={<Stations />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={looker} />}>
              <Route path="*" element={<Manufacture />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={root} />}>
              <Route path="testpage" element={<TestPage />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={looker} />}>
              <Route path="line/:id" element={<Line />} />
            </Route>
            <Route element={<RequireAuth allowedRoles={looker} />}>
              <Route path="node" element={<Node />} />
            </Route>
          </Route>
          <Route path="/login" element={<Login />} />
        </Routes>
      </ThemeCustomization>
    </QueryClientProvider>
  );
}

export default App;


