// material-ui
import { Collapse, List, ListItemButton, ListItemIcon, ListItemText, Typography, useTheme } from '@mui/material';

// project import
import { useEffect, useState } from 'react';
import NavItem from './NavItem';
import { useSelector } from 'react-redux';
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import { useLocation } from 'react-router-dom';

// ==============================|| DRAWER CONTENT - NAVIGATION ||============================== //

const NavCollapse = ({ item, level }) => {
    const { drawerOpen } = useSelector((state) => state.menu);
    const theme = useTheme();
    const { pathname } = useLocation();
    const [openCollapse, setOpenCollapse] = useState(false);
    const [selected, setSelected] = useState(null);

    const handleClick = () => {
        setOpenCollapse(!openCollapse);
        setSelected(!selected ? item.id : null);
    }

    const textColor = 'text.primary';
    const iconSelectedColor = 'primary.main';

    const Icon = item.icon;
    const itemIcon = item.icon ? <Icon style={{ fontSize: drawerOpen ? '1rem' : '1.25rem' }} /> : false;

    const checkOpenForParent = (child, id) => {
        child.forEach((item) => {
            if (item.url === pathname) {
                setOpenCollapse(true);
                setSelected(id);
            }
        });
    };

    // menu collapse for sub-levels
    useEffect(() => {
        setOpenCollapse(false);
        setSelected(null);
        if (item.children) {
            item.children.forEach((el) => {
                if (el.children?.length) {
                    checkOpenForParent(el.children, item.id);
                }
                if (el.url === pathname) {
                    setSelected(item.id);
                    setOpenCollapse(true);
                }
            });
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pathname, item.children]);


    return (
        <>
            <ListItemButton
                sx={{
                    zIndex: 1201,
                    pl: drawerOpen ? `${level * 28}px` : 1.5,
                    py: !drawerOpen && level === 1 ? 1.25 : 1,
                    ...(drawerOpen && {
                        '&:hover': {
                            bgcolor: 'primary.lighter'
                        },
                        '&.Mui-selected': {
                            bgcolor: 'transparent',
                            '&:hover': {
                                color: iconSelectedColor,
                                bgcolor: 'primary.lighter'
                            }
                        }
                    }),
                    ...(!drawerOpen && {
                        '&:hover': {
                            bgcolor: 'transparent'
                        },
                        '&.Mui-selected': {
                            '&:hover': {
                                bgcolor: 'transparent'
                            },
                            bgcolor: 'transparent'
                        }
                    })
                }}
                selected={selected === item.id}
                onClick={handleClick}
            >
                {itemIcon && (
                    <ListItemIcon
                        sx={{
                            minWidth: 28,
                            color: selected ? iconSelectedColor : textColor,
                            ...(!drawerOpen && {
                                borderRadius: 1.5,
                                width: 36,
                                height: 36,
                                alignItems: 'center',
                                justifyContent: 'center',
                                '&:hover': {
                                    bgcolor: 'secondary.lighter'
                                }
                            }),
                            ...(!drawerOpen &&
                                selected && {
                                bgcolor: 'primary.lighter',
                                '&:hover': {
                                    bgcolor: 'primary.lighter'
                                }
                            })
                        }}
                    >
                        {itemIcon}
                    </ListItemIcon>
                )}
                <ListItemText
                    primary={
                        <Typography variant="h6" sx={{ color: selected ? iconSelectedColor : textColor }}>
                            {item.title}
                        </Typography>
                    }
                />
                {openCollapse ? (
                    <UpOutlined style={{ fontSize: '0.625rem', color: theme.palette.primary.main }} />
                ) : (
                    <DownOutlined style={{ fontSize: '0.625rem', color: iconSelectedColor }} />
                )}
            </ListItemButton>
            <Collapse in={openCollapse} timeout="auto" unmountOnExit>
                <List sx={{ p: 0 }} component="div">
                    {item.children?.map((collapseItem) => <NavItem key={collapseItem.id} item={collapseItem} level={2} />)}
                </List>
            </Collapse>
        </>

    );
};

export default NavCollapse;
